.image {
  width: 120px;
}
.content-div {
  padding-left: 30px;
}
.content-div ul {
  padding: 0;
}
.title-all-device {
  padding: 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.title-all-device > li {
  padding: 10px 20px;
  box-shadow: 0 0 10px 0 #ddd;
  margin: 8px;
  display: flex;
  width: 31%;
  background: #fff;
}
.title-all-device > li > div:nth-child(1) {
  color: #333;
  font-size: 54px;
  padding: 10px;
  line-height: 34px;
}
.title-all-device > li > div:nth-child(2) {
  margin-left: 20px;
}
.title-all-device > li > div:nth-child(2) > span:nth-child(2) {
  display: block;
  border-top: 3px solid #6234FF;
  width: 20px;
  float: right;
  margin: 14px 0 0 0;
}
.title-all-device > li > div:nth-child(2) > span:nth-child(3) {
  display: block;
  margin-top: 24px;
  color: #9d9d9d;
}
.title-all-device > li .iconfont {
  display: block;
  text-align: right;
  font-size: 20px;
  color: #6234FF;
}
.device-second {
  display: flex;
  margin: 0 5px;
  padding: 0;
  margin-bottom: 20px;
}
.device-second > li {
  box-shadow: 0 0 10px 0 #ddd;
}
.device-second > li h3 {
  padding-top: 10px;
  padding-left: 20px;
}
.device-second > li:nth-child(1) {
  width: 50%;
  margin-right: 5px;
  background: #fff;
}
.device-second > li:nth-child(1) > div {
  display: flex;
}
.device-second > li:nth-child(1) > div > div:nth-child(1) {
  width: 83%;
}
.device-second > li:nth-child(2) {
  width: 60%;
  margin-left: 5px;
  background: #fff;
}
.device-second .second-tabs {
  float: right;
  margin-right: 20px;
}
.device-second1 {
  display: flex;
  margin: 0 5px;
  padding: 0;
  margin-bottom: 20px;
}
.device-second1 > li {
  width: 50%;
  box-shadow: 0 0 10px 0 #ddd;
}
.device-second1 > li h3 {
  padding-top: 10px;
  padding-left: 20px;
}
.device-second1 > li:nth-child(1) {
  margin-right: 5px;
  background: #fff;
}
.device-second1 > li:nth-child(1) > div {
  display: flex;
}
.device-second1 > li:nth-child(1) > div > div:nth-child(1) {
  width: 100%;
}
.device-second1 > li:nth-child(2) {
  margin-left: 5px;
  background: #fff;
}
.device-second1 .second-tabs {
  float: right;
  margin-right: 20px;
}
.device-second-tag span {
  display: block;
  text-align: right;
}
.device-second-tag span:nth-child(2n-1) {
  font-size: 30px;
  color: #333;
}
.device-second-tag span:nth-child(2n) {
  color: #9d9d9d;
}
.device-last {
  box-shadow: 0 0 10px 0 #ddd;
  margin: 10px 5px;
  background: #fff;
}
.device-last h3 {
  padding: 10px 0 0 20px;
}
